@import url(//netdna.bootstrapcdn.com/font-awesome/4.0.3/css/font-awesome.css);
:root {
  font-size: 60%;

  --color-background: #F0F0F7;
  --color-primary-lighter: #00a8eb;
  --color-primary-light: #01bddf;
  --color-primary: #1C1B29;
  --color-primary-dark: #774DD6;
  --color-primary-darker: #6842C2;
  --color-secundary: #7289da;
  --color-secundary-dark: #8ea1e1;
  --color-title-in-primary: #FFFFFF;
  --color-text-in-primary: #B9BBBE;
  --color-text-title: #32264D;
  --color-text-complement: #9C98A6;
  --color-text-base: #6A6180;
  --color-line-in-white: #E6E6F0;
  --color-input-background: #F8F8FC;
  --color-button-text: #FFFFFF;
  --color-box-base: #FFFFFF;
  --color-box-footer: #FAFAFC;
  --color-sidebar: #111111;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100vh;
}

body {
  background-color: #F0F0F7;
  background-color: var(--color-background);
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
}

body, input, button, textarea {
  font: 500 1.6rem Poppins;
  color: #6A6180;
  color: var(--color-text-base);
}

.container {
  width: 90vw;
  max-width: 700px;
}

@media (min-width: 700px){
  :root {
      font-size: 62.5%;
  }
}
#page-landing {
  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  color: var(--color-text-in-primary);
  background: var(--color-primary);
}

.hero-image {
  width: 100%;
}

.logo-container {
  text-align: center;
  margin-bottom: 3.2rem;
}

.logo-container h1 {
  font-size: 8rem;
  color: var(--color-title-in-primary);
}

.logo-container h2 {
  font-weight: 500;
  font-size: 2.4rem;
  line-height: 4.6rem;
  margin-top: 0.8rem;
}

.buttons-container {
  display: flex;
  justify-content: center;
  margin: 3.2rem 0;
}

.buttons-container a {
  width: 30rem;
  height: 10.4rem;
  border-radius: 0.8rem;
  font: 700 2rem Archivo;

  display: flex;
  align-items: center;
  justify-content: center;

  text-decoration: none;
  color: var(--color-button-text);

  transition: background-color 0.2s;
}

.buttons-container a:first-child {
  margin-right: 1.6rem;
}

.buttons-container a img {
  width: 4rem;
  margin-right: 2.4rem;
}

.buttons-container a.study {
  background: var(--color-primary-lighter);
}

.buttons-container a.ser-aluno {
  background: var(--color-secundary);
}

.buttons-container a.study:hover {
  background: var(--color-primary-light);
}

.buttons-container a.ser-aluno:hover {
  background: var(--color-secundary-dark);
}

@media (min-width: 1100px){
  #page-landing-content {
      max-width: 1100px;

      display: grid;
      grid-template-rows: 350px 1fr;
      grid-template-columns: 2fr 2fr;
      grid-template-areas: 
          "logo hero"
          "buttons hero"
      ;
  }
  .logo-container {
      grid-area: logo;
      align-self: center;
      margin: 0;
      text-align: left;
  }
  .logo-container h2 {
      text-align: left;
      text-align: initial;
      font-size: 3.2rem;
  }
  .hero-image {
      grid-area: hero;
      justify-self: end;
      align-self: center;
  }
  .buttons-container {
      grid-area: buttons;
      justify-content: flex-start;
  }
  .buttons-container a {
      font-size: 2.4rem;
  }
}
.fa-2x {
font-size: 2em;
}
.fa {
position: relative;
display: table-cell;
width: 85px;
height: 36px;
text-align: center;
vertical-align: middle;
font-size:24px;
}


.main-menu:hover,nav.main-menu.expanded {
width:250px;
overflow:visible;
}

.main-menu {
background:var(--color-sidebar);
border-right:5px solid var(--color-primary);
border-top-right-radius: 18px;
border-bottom-right-radius: 18px;
position:absolute;
top:0;
bottom:0;
height:100%;
left:0;
width:75px;
overflow:hidden;
transition:width 0.1s linear;
z-index:1000;
}

.main-menu>ul {
margin:16px 0;
}

.main-menu li {
position:relative;
display:block;
width:250px;
}

.main-menu li>a {
position:relative;
display:table;
border-collapse:collapse;
border-spacing:0;
color: #fff;
font-size: 16px;
text-decoration:none;
transition:all .2s linear;
}

.main-menu .nav-icon {
position:relative;
display:table-cell;
width:60px;
height:36px;
text-align:center;
vertical-align:middle;
font-size:18px;
}

.main-menu .nav-text {
position:relative;
display:table-cell;
vertical-align:middle;
width:190px;
}

.main-menu>ul.logout {
position:absolute;
left:0;
bottom:0;
}

.no-touch .scrollable.hover {
overflow-y:hidden;
}

.no-touch .scrollable.hover:hover {
overflow-y:auto;
overflow:visible;
}

a:hover,a:focus {
text-decoration:none;
}

nav {
-webkit-user-select:none;
-ms-user-select:none;
-o-user-select:none;
user-select:none;
}

nav ul,nav li {
outline:0;
margin:0;
padding:0;
}
.main-menu li:hover>a,nav.main-menu li.active>a,.dropdown-menu>li>a:hover,.dropdown-menu>li>a:focus,.dropdown-menu>.active>a,.dropdown-menu>.active>a:hover,.dropdown-menu>.active>a:focus,.no-touch .dashboard-page nav.dashboard-menu ul li:hover a,.dashboard-page nav.dashboard-menu ul li.active a {
color:#fff;
background-color:var(--color-primary-darker);
}

